import { useState } from "react";
import "./styles.css";

export default function Count() {
  const [num, setNum] = useState(0);
  return (
    <div>
      {num}
      <div onClick={() => setNum((p) => p + 1)}>+</div>
      <div onClick={() => setNum(0)}>reset</div>
      <div onClick={() => setNum((p) => p - 1)}>-</div>
    </div>
  );
}
